@use 'base/variables' as v;
@use 'base/mixins' as m;

.process-coffee {
    margin-bottom: v.$separation;
    @include m.tablet { 
        display: flex;
        gap: v.$separation;
        align-items: center;
    }
    &:last-of-type {
        margin-bottom: 0;
    }
    &:nth-child(even) {
        .text {
            order: -1;
        }
    }
    .image,
    .text {
        flex: 1;
    }
    .text {
        margin-top: 1rem;
        h3 {
            margin: 0;
            text-align: left;
            color: v.$primary;

            &::after {
                content: '';
                display: block;
                width: 50%;
                height: 5px;
                background-color: v.$secondary ;
            }
        }
    }
}